import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import router from './router'
import NoSleep from "nosleep.js/dist/NoSleep.min.js";

Vue.prototype.$NoSleep = NoSleep;
Vue.config.productionTip = false

new Vue({
    vuetify,
    router,
    render: h => h(App)
}).$mount('#app')


